<template>
    <h1>Dashboard</h1>
    <p>click on one action to remove it</p>
  <div>
    <table class="table-hover">   
      <tr v-for="item in list" :key="item">
        <td>
          <a v-on:click="remove(item.metadata.idChaine)">
          <div class="data">
            <p>ID : {{item.metadata.idChaine}}</p>
            <p>Action type : {{ item.action.name }}</p>
            <p>Reaction type : {{ item.reaction.name }}</p>
          </div>
          </a>
        </td>
      </tr>
      <tr>
        <td>
          <a href="/actions">
            <div class="data">
              <p style="font-size:x-large;">+</p>
            </div>
          </a>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
  
  export default {
    name: 'DashboardActions',
    components: {
    },
    created() {
      var myHeaders = new Headers();
      myHeaders.append("token", localStorage.token);
      var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
      };
      fetch("http://localhost:8080/ar-user/listMyAR/", requestOptions)
        .then(res => {
          res.json().then((ans) => {
              this.list = ans["myars"];
            })
        })
        .catch(error => console.log('error', error));
    },
    methods: {
      remove(id) {
        var myHeaders = new Headers();
        myHeaders.append("token", localStorage.token);
              
        var requestOptions = {
          method: 'DELETE',
          headers: myHeaders,
          redirect: 'follow'
        };

        fetch("http://localhost:8080/ar-user/chain/" + id, requestOptions)
          .then(response => {
            response.text();
            location.reload();
          })
          .then(result => console.log(result))
          .catch(error => console.log('error', error));
      }
    },
    data() {
      return {
        list: []
      }
    },
  };
</script>

